$prefix: ".wc-booster";


#{$prefix}-countdown-wrapper {
    display: flex;
    flex-wrap: wrap;
    width: fit-content;

    .countdown-item {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }
}

.expired-message {
    font-size: 18px;
    font-weight: 500;
}


#{$prefix}-countdown-block.right {
    text-align: -webkit-right;
}

#{$prefix}-countdown-block.left {
    text-align: -webkit-left;
}

#{$prefix}-countdown-block.center {
    text-align: -webkit-center;
}